@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.App {
  text-align: center;
}

#mui-1 {
  background-color: darkgreen !important;
  font-weight: 900;
  color: white;
}

.MuiGrid-root p {
  color: black !important;
  font-weight: 600 !important;
}

.MuiGrid-root h6 {
  color: black;
}
body {
  background-image: url(/static/media/Matrix_1.ac5bbc41.gif);
  background-size: contain;
  background-attachment: fixed !important;
  background-repeat: repeat;
  background-position: 50% 50%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Import fonts */

@font-face {
    font-family: ShowCardGothic;
    src: url(/static/media/ShowCardGothic.464936a8.TTF);
}

@font-face {
    font-family: PoorRichard;
    src: url(/static/media/PoorRichard.0c9803a1.TTF);
}

/* Tag to make something screen-reader only */
.sr-only {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}

/* General settings */
* {
    font-family: Roboto;
    font-weight: 400;
}

html {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-size: 16px;
    background-image: url(/static/media/Matrix_1.ac5bbc41.gif);
    background-size: contain;
    background-attachment: fixed !important;
    background-repeat: repeat;
    background-position: 50% 50%;
}

/* Begin page element settings*/

/* Page container; this is essentially to make sure the footer is always at least at
    the bottom of the page */
#page-container {
    position: relative;
    min-height: 100vh;
}

/* This allows the room for the footer at the bottom */
#content-wrap {
    padding-bottom: 2.7rem;
}

/* Our header settings */
.main-header {
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background: rgb(11, 11, 11);
    background: linear-gradient(90deg, rgba(11, 11, 11, 1) 0%, rgba(31, 31, 31, 1) 82%, rgba(55, 55, 55, 1) 100%);
    border-bottom: 2px solid black;
    overflow: hidden;
}

.header-space {
    display: none;
}

/* General nav settings */
.nav a {
    text-decoration: none;
    color: whitesmoke;
}

.nav a:hover {
    color: rgba(34, 255, 0, 0.3);
}

/* Top nav settings */
.main-nav {
    text-align: right;
    font-size: 1.1em;
}

.main-nav ul {
    margin: 5px 5px 5px 0;
}

.main-nav li {
    padding-left: 0.5%;
    padding-right: 0.5%;
    display: inline;
}

.hamburger-button { /* For mobile or short displays */
    display: none;
}

.navbar-logo {
    position: absolute;
    height: 100px;
    top: 5px;
    left: 5px;
    border-radius: 10px;
    border:1.5px solid #212121;
}

/* Various header settings */
.generic-header {
    background-color: rgba(0, 0, 0, 0.3);
    font-size: 34px;
    border-radius: 5px;
    padding: 5px 10px 5px 10px;
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

/* Various layouts for displaying information */
/* A single box */
.single-box {
    /* Exactly the same as default settings */
    background-color: rgba(145, 145, 145, 0.3);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    padding: 10px 10px 10px 10px;
    border-radius: 5px;
    background-size: cover;
    text-align: justify;
    margin-left: 15vw;
    margin-right: 15vw;
    margin-bottom: 10px;
}

.single-box-wide {
    /* Exactly the same as default settings */
    background-color: rgba(145, 145, 145, 0.3);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    padding: 10px 10px 10px 10px;
    border-radius: 5px;
    background-size: cover;
    text-align: justify;
    margin-left: 7.5vw;
    margin-right: 7.5vw;
    margin-bottom: 10px;
}

/* Side by side, two boxes */
.double-box {
    margin-left: 5vw;
    margin-right: 5vw;
    margin-bottom: 10px;
}

.double-box span {
    display: flex;
}

.double-box span p {
    width: 40%;
}

.double-box-box-1 {
    margin: auto;
    margin-right: 0.25%;
}

.double-box-box-2 {
    margin: auto;
    margin-left: 0.25%;
}

/* Side by side, three boxes */
.triple-box {
    margin-left: 5vw;
    margin-right: 5vw;
    margin-bottom: 10px;
}

.triple-box span {
    display: flex;
}

.triple-box span p {
    width: 25%;
}

.triple-box-box-1 {
    margin: auto;
    margin-right: 0.2%;
}

.triple-box-box-2 {
    margin: auto;
    margin-right: 0.25%;
    margin-left: 0.25%;
}

.triple-box-box-3 {
    margin: auto;
    margin-left: 0.25%;
}

/* FAQ drop down menu */
.faq-drop-down {
    padding: 1%;
    background-color: rgba(145, 145, 145, 0.3);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    border-radius: 5px;
    text-align: center;
    width: 90%;
    font-size: 16px;
}

.faq-question {
    text-align: -webkit-center !important;
    text-align: center;
}

.faq-answer li {
    margin-right: 25px;
    padding-bottom: 5px;
}

.faq-answer li:last-child {
    padding-bottom: 0;
}

/* For if there is a connect wallet section */
.wallet-connect {
    text-align: center;
}

.wallet-connect span {
    padding: 0px 2.5px 0px 2.5px;
    display: inline-block;
}

.wallet-connect button {
    aspect-ratio: 1;
    width: 60px;
}

/* Specific wallet buttons */
.metamask-button {
    background: url("/static/media/MetaMask Fox White.f2bdc1a6.png");
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
}

.metamask-button[aria-expanded="true"] {
    background: url("/static/media/MetaMask Fox.30289624.png");
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
}

.phantom-button {
    background: url("/static/media/Phantom Wallet White.d5281b81.png");
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
    width: 50px !important;
}

.phantom-button[aria-expanded="true"] {
    background: url("/static/media/Phantom Wallet.3efcee43.png");
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
}

/* If there is a roadmap table (really just for roadmap page*/
.roadmap-container {
    margin-bottom: 10px;
}

.roadmap-table {
    padding-left: 10px;
    padding-right: 10px;
}

.roadmap-row {
    border-bottom: 2px dashed black;
    padding-bottom: 1em;
    margin-bottom: 1em;
    padding-left: 5px;
    padding-right: 5px;
    display: flex;
}

.roadmap-row:first-child {
    margin-top: 0.5em;
}

.roadmap-row:last-child {
    border-bottom: none;
    margin-bottom: -0.5em;
}

.roadmap-item {
    padding-right: 0.5em;
    display: inline-block;
}

.roadmap-date {
    color: #212121;
    font-weight: bold;
    width: 11%;
    margin-right: auto;
    font-size: 16px;
}

.roadmap-img-container {
    text-align: center;
    padding-top: 5px;
    padding-bottom: 10px;
}

.roadmap-img {
    width: min(450px, 90%);
    margin-left: auto;
    border-radius: 5px;
    border: #212121 solid 2.5px;
}

/* Timer section */
.countdown {
    margin-left: 5vw;
    margin-right: 5vw;
    margin-bottom: 10px;
    text-align: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.countdown div {
    width: 25%;
    text-align: center;
    font-size: 20px;
}

.countdown-values {
    font-size: 35px !important;
    color: #008015;
    font-weight: 700;
}

/* About Us */
.about-them {
    background-color: transparent;
    -webkit-backdrop-filter: blur(0rem);
    backdrop-filter: blur(0rem);
    margin: 0;
    padding-top: 10px;
    min-height: 100px;
}

.profile-pic {
    width: 120px;
    height: 150px;
    border-radius: 50%;
    border: #212121 solid 4px;
    text-align: center;
    float: left;
    shape-outside: circle();
    margin: 0 20px 20px 0;
    z-index: 5000;
}

.name {
    font-size: 20px;
    font-weight: 1000;
    margin: 5px 0 0 0;
    padding: 0;
}

.role {
    font-size: 15px;
    font-weight: 700;
    margin: 5px 0 0 0;
}

/* Footer settings */
/* Part of the little logo in the corner, could be replaced with an
    image at some point */
.identi-footer {
    font-family: ShowCardGothic;
    font-weight: 100;
    color: whitesmoke;
}

.three-footer {
    font-family: PoorRichard;
    font-size: x-large;
    color: #008015;
}

.main-footer {
    border-radius: 5px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    background: rgb(11, 11, 11);
    background: linear-gradient(90deg, rgba(11, 11, 11, 1) 0%, rgba(31, 31, 31, 1) 82%, rgba(55, 55, 55, 1) 100%);
    border-top: 2px solid black;
    overflow: hidden;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2.7rem; /* If this needs to change, content-wrapper also needs to change */
}

.main-footer ul {
    margin-left: 0;
    padding: 0;
    display: flex;
    position: relative;
    top: -20px;
}

.main-footer ul li {
    display: inline-block;
    list-style-type: none;
    padding-top: 13px;
    padding-left: 5px;
}

.main-footer ul li:first-child {
    margin-right: auto;
    padding-left: 15px;
    position: relative;
    top: -20px;
}

.main-footer ul li:last-child {
    padding-right: 20px;
}

.twitter-button {
    position: relative;
    top: 2px;
}

/* Mobile View Stuff */

@media (max-width: 600px) {
    .header-space {
        display: block;
        height: 30px;
        border-radius: 5px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        background: rgb(11, 11, 11);
        background: linear-gradient(90deg, rgba(11, 11, 11, 1) 0%, rgba(31, 31, 31, 1) 82%, rgba(55, 55, 55, 1) 100%);
        border-bottom: 2px solid black;
    }

    .main-header {
        border-bottom: 0vw solid black;
    }

    .primary-navigation {
        position: fixed;
        inset: 0 0 0 50%;
        background: rgba(0, 0, 0, 0.1);
        -webkit-backdrop-filter: blur(0.5rem);
        backdrop-filter: blur(0.75rem);
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: -webkit-transform 350ms ease-out;
        transition: transform 350ms ease-out;
        transition: transform 350ms ease-out, -webkit-transform 350ms ease-out;
        z-index: 8000;
    }

    .primary-navigation[data-visible="true"] {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }

    .main-nav li {
        display: flex;
        flex-direction: column;
        padding-left: 0%;
        padding-right: 0%;
        padding-top: 7%;
    }

    .main-nav {
        font-size: 12px;
    }

    .main-nav a {
        color: #212121;
        font-weight: 800;
    }

    .main-nav ul {
        margin: 0;
        padding-right: 1rem;
        padding-top: max(6vw, 18px);
    }

    .hamburger-button {
        display: block;
        position: absolute;
        background: url(data:image/png;base64,);
        background-repeat: no-repeat;
        background-size: cover;
        border: 0;
        width: max(4.5vw, 12px);
        aspect-ratio: 1;
        top: max(0.25vw, 2.5px);
        right: min(2vw, 10px);
        z-index: 9999;
    }

    .hamburger-button[aria-expanded="true"] {
        display: block;
        position: absolute;
        background: url(data:image/png;base64,);
        background-repeat: no-repeat;
        background-size: cover;
        border: 0;
        width: max(4.5vw, 12px);
        aspect-ratio: 1;
        top: max(0.25vw, 2.5px);
        right: min(2vw, 10px);
        z-index: 9999;
    }

    .normal-text {
        font-size: 12px;
    }

    .generic-header {
        font-size: 16px;
    }

    .navbar-logo {
        height: 60px;
    }

    .first-header {
        margin-top: 40px;
    }

    .about-them {
        font-size: 12px;
        font-weight: 300;
    }
    
    .profile-pic {
        width: 80px;
        height: 100px;
    }
    
    .name {
        font-size: 14px;
        font-weight: 1000;
        margin: 5px 0 0 0;
        padding: 0;
    }
    
    .role {
        font-size: 12px;
        font-weight: 700;
        margin: 5px 0 0 0;
    }

    .faq-drop-down {
        font-size: 12px;
    }
}
